export default class Header {
  constructor() {
    this.headerModal = document.querySelector('.js__header-modal');
    this.headerModalClass = 'header__modal--opened';

    this.burgerButtonOpen = document.querySelector('.js__header-burger-open') || null;
    this.burgerButtonClose = document.querySelector('.js__header-burger-close') || null;

    this.init();
  }

  init() {
    this.openHeaderModal();
    this.closeHeaderModal();
  }

  openHeaderModal() {
    if(!this.burgerButtonOpen) return;
    const that = this;

    this.burgerButtonOpen.addEventListener('click', openModal, true);

    function openModal() {
      that.headerModal.classList.add(that.headerModalClass);
    }
  }

  closeHeaderModal() {
    if(!this.burgerButtonClose) return;
    const that = this;

    this.burgerButtonClose.addEventListener('click', closeModal, true);

    function closeModal() {
      that.headerModal.classList.remove(that.headerModalClass);
    }
  }
}
